let _self;
export default {
	data() {
		const currentDate = this.getDate({
			format: true
		});
		return {
			isok: true,
			date: currentDate,
			index: 0,
			reMark: '',
			reson:'',
			ageArr: ['01', '02', '03', '04', '05', '06', '07', '08', '09', '10'],
			resonArr: ['意外', '病死', '自然死亡', '其他']
		};
	},
	onLoad() {
		_self = this;
	},
	computed: {
		startDate() {
			return this.getDate('start');
		},
		endDate() {
			return this.getDate('end');
		}
	},
	methods: {
		petType() {
			uni.navigateTo({
				url: '../../pet/petType/petType'
			});
		},
		petAge() {},
		bindPickerChange(e) {
			console.log('picker发送选择改变，携带值为', e.detail.value);
			_self.index = e.detail.value;
		},
		getDate(type) {
			const date = new Date();
			let year = date.getFullYear();
			let month = date.getMonth() + 1;
			let day = date.getDate();

			if (type === 'start') {
				year = year - 60;
			} else if (type === 'end') {
				year = year + 2;
			}
			month = month > 9 ? month : '0' + month;
			day = day > 9 ? day : '0' + day;
			return `${year}-${month}-${day}`;
		},
		//  选择日期
		bindDateChange(e) {
			_self.date = e.target.value;
			console.log(_self.date);
		},
		//选择原因
		bingResonChange(e){
			_self.reson=e.target.value;
			console.log(_self.reson)
			},
		getAddress() {
			uni.chooseLocation({
				success: e => {
					console.log(e);
				}
			});
		}
	}
};
